<template>
  <div class="clock_records page">
    <div class="header">
      <div class="flex1" @click="changeMonth('prev')">
        <img src="@/assets/imgs/houtui.png" alt="">
      </div>
      <div class="flex2">
        {{nowMonth | moment('YYYY年MM月')}}
      </div>
      <div class="flex1" @click="changeMonth('next')">
        <img src="@/assets/imgs/qianjin.png" alt="">
      </div>
    </div>
    <div class="scroll_content_box">
      <item v-for="(item, key) in records" :key="item.id + 'xx' + key" :clockItem="item"></item>
      <no-item v-if="records.length===0"></no-item>
      <div class="blank" v-else></div>
    </div>
  </div>
</template>

<script>
import Item from './Item'
export default {
  name: 'ClockRecords',
  components: {
    Item
  },
  data () {
    return {
      records: [
        {
          id: 1,
          clockType: '上班打卡',
          time: new Date(),
          line_id: '一号线',
          site_id: '后卫寨'
        }
      ],
      nowMonth: new Date()
    }
  },
  methods: {
    changeMonth (flag) {
      switch (flag) {
        case 'prev':
          this.nowMonth = this.$moment(this.nowMonth).subtract(1, 'month')
          break
        case 'next':
          this.nowMonth = this.$moment(this.nowMonth).add(1, 'month')
          break
        default:
          break
      }
    },
    getShedulingData () {
      this.$rpc.call('fuenc_station.clock_record', 'get_month_clock_record', [], {
        month: this.$moment(this.nowMonth).format('YYYY-MM-01')
      }).then(res => {
        this.records = res
      }).catch(e => {
        console.log(e)
        alert('获取打卡记录失败')
      })
    }
  },
  mounted () {
    this.getShedulingData()
  },
  watch: {
    nowMonth (v) {
      this.getShedulingData()
    }
  }
}
</script>

